li{
   list-style: none; 
}

.partners-logo-container{
    /* border:1px red solid; */
    max-width:100%;
    padding:10px;
    margin:75px 40px;
    box-shadow:1px 1px 8px 3px #dfdfec; 
}
.img-cont{
    display:grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}

.partners-logo-button{
    border: 1px solid rgba(170,168,255,.3);
    background:transparent;
    color:#4d4d66;
    padding:10px 15px;
    border-radius: 20px;
}
.partners-logo-button:hover{
    background-color: rgba(170,168,255,.09);
    cursor: pointer;
}
.partners-logo-item{
    width:220px;
    /* margin:20px; */
    padding: 10px;
}

.btn-cont{
    display: flex;
    justify-content: center;
    margin-top: 100px;
    margin-bottom: 60px;
}


.learn-more{
    font-family: 'Figtree', sans-serif;
    margin-left: auto;
    margin-right: auto;
    display:flex;
    justify-content: space-around;
    align-items: center;
    /* border:1px black solid; */
    max-width: 800px;
    font-size: 16px;
    box-shadow: 1px 1px 8px rgb(184, 183, 183);
    border-radius: 12px;
    padding: 24px 60px;
    line-height: 1.2rem;
}

.learn-more>button{
    width:250px;
    padding:24px 48px;
    background-color: rgb(66, 48, 221, 1);
    border:none;
    font-size: 16px;
    font-weight: bold;
    color:white;
    font-family: 'Figtree', sans-serif;
    box-shadow:6px 6px #1c1279;
    transition: all 0.3s;
    border-radius: 12px;
}
.learn-more>button:hover{
    box-shadow: none;
    transform: translate(8px, 8px);
}